$mainColor: #100b94;

body {
	background: url(./images/bg1.jpg);
	background-position-y: top;
	background-size: auto;
	background-repeat: no-repeat;
}
body.Clouds {
	background: url(./images/clouds.jpg) center;
	background-size: auto;
}
body.Snow {
	background: url(./images/snow.jpg);
	background-size: auto;
}
body.Rain {
	background: url(./images/rain.jpg);
	background-size: auto;
	header h1 {
		color: #ccc;
	}
}
body.Clear {
	background: url(./images/bg.jpg);
	background-size: auto;
}
a {
	color: #2437e4;
}
@mixin heading {
	position: relative;
	&::after {
		content: ' ';
		width: 200px;
		padding: 2px;
		position: absolute;
		bottom: 35px;
		left: 0;
		right: 0;
		margin: auto;
		background: $mainColor;
	}
}
header {
	padding: 10px;
	text-align: center;
	h1 {
		color: #111;
		padding: 40px 30px;
		font-size: 48px;
		@include heading;
	}
}
#searchform {
	display: flex;
	padding: 10px;
	justify-content: center;
	input {
		width: 90%;
		padding: 13px;
		margin: 5px;
		font-size: 17px;
		border: 1px solid #aaa;
		border-radius: 5px;
		min-width: 200px;
		max-width: 500px;
		&:focus {
			outline: none;
			border: 1px solid $mainColor;
		}
	}
	button {
		color: #fff;
		margin: 5px;
		border: none;
		font-size: 19px;
		font-weight: bold;
		border-radius: 5px;
		padding: 10px 20px;
		cursor: pointer;
		background: $mainColor;
		&:hover {
			background: lighten($color: $mainColor, $amount: 10%);
		}
	}
}

main {
	display: grid;
	width: 95%;
	margin: auto;
	margin-top: 30px;
	text-align: center;
	border-radius: 10px;
	max-width: 600px;
	min-height: 280px;
	align-items: center;
	backdrop-filter: blur(2px);
	background: rgba(255, 255, 255, 0.5);
	#error {
		color: #fc0101;
		font-size: 30px;
	}
}
#result {
	#place {
		font-size: 40px;
		padding: 10px;
	}
	#temp {
		color: $mainColor;
		padding: 10px;
		font-size: 80px;
		font-weight: bold;
	}
	#temp span {
		font-size: 70px;
	}
	#weather {
		padding: 10px;
		font-size: 30px;
	}
}
#preloader {
	display: grid;
	.loader06 {
		width: 66px;
		height: 66px;
		border: 4px solid transparent;
		border-radius: 50%;
		position: relative;
		top: 50%;
		margin: -28px auto 0;
	}
	.loader06::before {
		content: '';
		border: 4px solid rgba(16, 11, 148, 0.5);
		border-radius: 50%;
		width: 77.2px;
		height: 77.2px;
		position: absolute;
		top: -9.6px;
		left: -9.6px;
		animation: loader-scale 1s ease-out infinite;
		animation-delay: 1s;
		opacity: 0;
	}
	.loader06::after {
		content: '';
		border: 4px solid #100b94;
		border-radius: 50%;
		width: 66px;
		height: 66px;
		position: absolute;
		top: -4px;
		left: -4px;
		animation: loader-scale 1s ease-out infinite;
		animation-delay: 0.5s;
	}
	@keyframes loader-scale {
		0% {
			transform: scale(0);
			opacity: 0;
		}
		50% {
			opacity: 1;
		}
		100% {
			transform: scale(1);
			opacity: 0;
		}
	}
}

footer {
	color: #ddd;
	font-size: 19px;
	width: 100%;
	position: absolute;
	margin-top: 80px;
	padding: 15px;
	text-align: center;
	background: #01000c;
}
@media (min-height: 680px) {
	footer {
		bottom: 0;
		position: absolute;
	}
}
